.App {
  text-align: center;
  font-family: 'Avengeance-Regular'
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}
.Clock-bg {
  margin-top: 20px;
  height: 130px;
  width: 1000px;
}

.Clock-title {
  margin-top: 50px;
  font-size: 40px;
  font-weight: 800px;
  color: #000099;
}

.App-header {
  background-color: #050505;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.Clock {
  display:-ms-inline-flexbox;
  display:inline-flex; 
  -ms-flex-direction:row; 
      flex-direction:row;
  margin-top: 20px;
}

.Clock-number {
  padding: 10;
  font-size: 28px;
  font-weight: bold;
}

.Clock-unit {
  padding: 10;
  font-size: 25px
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



